import React from 'react';
import Home from '../components/Home';
import About from '../components/About';
import Contact from '../components/Contact';
import Login from '../components/Login';
import NotFound from '../components/NotFound';

const APP_NAME = 'booking-app'

function routeLink (link) {
    return APP_NAME + link;
}

const routes = [
    {
        path : routeLink('/'),
        exact : true,
        main : () => <Home />
    },
    {
        path : routeLink('/about'),
        exact : false,
        main : () => <About />
    },
    {
        path : routeLink('/contact'),
        exact : false,
        main : () => <Contact />
    },
    {
        path : routeLink('/notfound'),
        exact : false,
        main : () => <NotFound />
    },
    // {
    //     path : '/products',
    //     exact : false,
    //     main : ({ match, location }) => <Products match={match} location={location} />
    // },
    {
        path : routeLink('/login'),
        exact : false,
        main : ({location}) => <Login location={location} />
    }
];

export default routes;